*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}



:root {
  --general-font: "DM Sans", sans-serif;
  --heading-font: "Titan One", cursive;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
  --Ice-cream-color: #ffb8ca;
  --Brand-color: #d41443;
  --general-white: #fff;
}

.bubbles {
  width: 100%;
  height: 100%;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.bubble {
  opacity: .5;
  background: #f1f1f1;
  border-radius: 50%;
  animation: flying 10s ease-in infinite;
  position: absolute;
  bottom: -75%;
}

.bubble:nth-child(1) {
  width: 40px;
  height: 40px;
  animation-duration: 8s;
  left: 10%;
}

.bubble:nth-child(2) {
  width: 20px;
  height: 20px;
  animation-duration: 5s;
  animation-delay: 1s;
  left: 20%;
}

.bubble:nth-child(3) {
  width: 50px;
  height: 50px;
  animation-duration: 10s;
  animation-delay: 2s;
  left: 35%;
}

.bubble:nth-child(4) {
  width: 80px;
  height: 80px;
  animation-duration: 7s;
  animation-delay: 0s;
  left: 50%;
}

.bubble:nth-child(5) {
  width: 35px;
  height: 35px;
  animation-duration: 6s;
  animation-delay: 1s;
  left: 55%;
}

.bubble:nth-child(6) {
  width: 45px;
  height: 45px;
  animation-duration: 8s;
  animation-delay: 3s;
  left: 65%;
}

.bubble:nth-child(7) {
  width: 25px;
  height: 25px;
  animation-duration: 7s;
  animation-delay: 2s;
  left: 75%;
}

.bubble:nth-child(8) {
  width: 80px;
  height: 80px;
  animation-duration: 6s;
  animation-delay: 1s;
  left: 80%;
}

.bubble:nth-child(9) {
  width: 15px;
  height: 15px;
  animation-duration: 9s;
  animation-delay: 0s;
  left: 70%;
}

.bubble:nth-child(10) {
  width: 50px;
  height: 50px;
  animation-duration: 5s;
  animation-delay: 3s;
  left: 85%;
}

@keyframes flying {
  0% {
    bottom: -100px;
    transform: translateX(0);
  }

  50% {
    transform: translateX(100px);
  }

  100% {
    bottom: 1080px;
    transform: translateX(-200px);
  }
}

header a {
  color: inherit;
  transition: var(--transition);
  text-decoration: none;
}

header a:visited {
  color: inherit;
}

header ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

header {
  height: 502px;
  width: 100%;
  background: var(--Ice-cream-color);
  z-index: 1;
  position: relative;
  overflow-x: hidden;
}

.wrapper {
  width: 100%;
  background: linear-gradient(45deg, #e17992, #ffbed1, #e17992, #f7d6b1, #e17992) 0 0 / 300% 100%;
  animation: gradient 16s linear infinite alternate;
}

@keyframes gradient {
  0% {
    background-position: 0%;
  }

  100% {
    background-position: 100%;
  }
}

.header-container-nav {
  z-index: 3;
  justify-content: space-between;
  align-items: center;
  padding: 8px 20px 0;
  display: flex;
}

.menu-button {
  background: none;
  border: none;
  display: flex;
}

.submit-btn {
  cursor: pointer;
  font: var(--general-font);
  color: var(--Brand-color);
  text-align: center;
  height: 44px;
  background: #fff;
  border: none;
  border-radius: 22px;
  padding: 0 43px;
  font-size: 16px;
  font-weight: 700;
}

.menu-pc, .header-buttons .submit-btn {
  display: none;
}

.submit-btn-content {
  justify-content: center;
  align-items: center;
  gap: 14px;
  display: flex;
}

.submit-btn:hover, .submit-btn:focus {
  transition: var(--transition);
  background: #ecebeb;
  transform: translateY(-3px);
}

.submit-btn:active {
  background: var(--general-white);
  scale: 95%;
}

.hero-buttons {
  gap: 9px;
  margin-top: 20px;
  display: flex;
}

.hero-btn {
  background: var(--general-white);
  cursor: pointer;
  font: var(--general-font);
  color: var(--Brand-color);
  text-align: center;
  height: 34px;
  border: none;
  border-radius: 22px;
  padding: 0 20px;
  font-size: .6em;
  font-weight: 400;
}

.hero-btn-content {
  justify-content: center;
  align-items: center;
  gap: 14px;
  display: flex;
}

.hero-btn:hover, .hero-btn:focus {
  transition: var(--transition);
  background: #ecebeb;
  transform: translateY(-3px);
}

.hero-btn:active {
  background: var(--general-white);
  scale: 95%;
}

.hero-btn-red {
  background: var(--Brand-color);
  cursor: pointer;
  font: var(--general-font);
  color: var(--general-white);
  text-align: center;
  height: 34px;
  border: none;
  border-radius: 22px;
  padding: 0 20px;
  font-size: .6em;
  font-weight: 400;
}

.hero-btn-red-content {
  justify-content: center;
  align-items: center;
  gap: 14px;
  display: flex;
}

.hero-btn-red:hover, .hero-btn-red:focus {
  transition: var(--transition);
  background: #be0e3a;
  transform: translateY(-3px);
}

.hero-btn-red:active {
  background: var(--Brand-color);
  scale: 95%;
}

.arrow-icon {
  height: 8px;
  width: 4px;
}

.menu-drawer-navigation {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.menu-drawer {
  width: 100%;
  margin-top: 19px;
}

.menu-drawer .link:hover, .menu-drawer .link:active {
  color: var(--Brand-color);
}

.menu-drawer li {
  font-size: 1em;
  font-weight: 700px;
  color: var(--general-white);
  font-family: var(--general-font);
  border-top: 1px solid #e18298;
  padding: 13px 0 13px 20px;
}

.menu-drawer li.last-link {
  border-bottom: 1px solid #e18298;
}

.js-menu-container {
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: #e17992;
  flex-direction: column;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.js-menu-container.is-open {
  transform: translateX(0);
}

.js-menu-container .js-close-menu {
  color: var(--general-white);
  cursor: pointer;
  background: none;
  border: none;
  align-self: flex-end;
  margin-top: 20px;
  margin-right: 20px;
}

.js-menu-container .js-close-menu:hover, .js-menu-container .js-close-menu:focus {
  color: var(--Brand-color);
  box-shadow: none;
  background: none;
}

.hero-image {
  height: 410px;
  z-index: -1;
  animation: slideInFromLeft .7s;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 45%;
}

@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100vw);
  }

  100% {
    transform: translateX(0);
  }
}

.elipse {
  width: 345px;
  height: 345px;
  z-index: -2;
  background: #ffa5ba;
  border-radius: 50%;
  margin-left: -80px;
  transition: all 1s;
  animation: elipseScale .7s;
  display: flex;
  position: absolute;
  bottom: 75px;
  left: 45%;
}

@keyframes elipseScale {
  0% {
    scale: 0%;
  }

  100% {
    scale: 100%;
  }
}

.elipse:hover {
  transform: scale(130%);
}

.header-title {
  margin-top: 136px;
  margin-left: 20px;
}

.milk-header, .right-header-container {
  display: none;
}

.link-how-its-made {
  width: 26px;
  transition: var(--transition);
}

.link-how-its-made:hover, .link-how-its-made:focus {
  cursor: pointer;
  rotate: 90deg;
  scale: 140%;
}

.link-how-its-made:active {
  scale: 120%;
}

.left-header-container h1 {
  color: #fff;
  font-family: var(--heading-font);
  letter-spacing: 1.04px;
  text-transform: uppercase;
  margin: 0;
  font-size: 26px;
  font-weight: 400;
}

.passion {
  color: var(--Brand-color);
  font-family: var(--heading-font);
  text-transform: uppercase;
  font-size: 48px;
  font-weight: 400;
  line-height: 96.5%;
}

